export const refreshConnection = () => {
	return (dispatch) => {
		var companyLocal = JSON.parse(localStorage.getItem("company-s10"));
		if (companyLocal) {
			dispatch(connectSignalr(companyLocal.Token))
		} else {
			dispatch(connectSignalr('error!!'))
		}
	};
};
