import { useSelector } from 'react-redux';
import 'devextreme/dist/css/dx.light.css';
import Button1 from 'devextreme-react/button';
import TreeList, {
	Pager,
	Paging,
	Editing,
	HeaderFilter,
	FilterPanel,
	FilterRow,
	Scrolling,
	Column,
	SearchPanel
} from 'devextreme-react/tree-list';
import { useState } from 'react';
import { Template } from 'devextreme-react';
import { CellNormal } from './Cellrend';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Paper from '@mui/material/Paper';
import Draggable from 'react-draggable';


function PaperComponent(props) {
	return (
	  <Draggable
		handle="#draggable-dialog-title"
		cancel={'[class*="MuiDialogContent-root"]'}
	  >
		<Paper {...props} />
	  </Draggable>
	);
  }

const BuscaMoneda = ({ tipo='',presupuestoN,show, setShow }) => {
	const handleClose = () => setShow(false);
	const proyects = useSelector((state) => state.proyects);
	const [monedaSel, setMonedaSel] = useState({
		Codigo: '',
		Descripcion: '',
	});
	

	const seleccionar = () => {
		if (monedaSel.Descripcion===''){
			return;
		}
			presupuestoN.CodMoneda=monedaSel.Codigo;
			presupuestoN.Moneda=monedaSel.Descripcion;
			setShow(false);
	}



	return (
		<>
			<Dialog
				open={show}
				onClose={handleClose}
				PaperComponent={PaperComponent}
				aria-labelledby="draggable-dialog-title"
				maxWidth={'sm'}
			>
				<DialogTitle style={{ cursor: 'move', background: '#398bf7', color: 'white', marginBottom: '10px', height: '50px' }} id="draggable-dialog-title">
					Selecciona una Moneda
					<Button1
						variant="primary"
						onClick={handleClose}
						style={{ position: 'absolute', right: '15px', top: '8px', background: '#398bf7', color: 'white' }}
					><i class="fas fa-times"></i>
					</Button1>
				</DialogTitle>

				<DialogContent >

					<DialogContentText>
						<TreeList
							dataSource={proyects.DataMonedas}
							keyExpr="CodMoneda"
							showBorders={true}
							focusedRowEnabled={true}
							defaultExpandedRowKeys={[1, 2, 3, 5]}
							columnAutoWidth={false}
							rootValue={-1}
							rowAlternationEnabled={true}
							onFocusedRowChanged={(e) => {
								console.log(e)
								setMonedaSel({
									Codigo: e.row.data.CodMoneda,
									Descripcion: e.row.data.Descripcion,
								});
							}}
							wordWrapEnabled={true}
						>
							<SearchPanel visible={true} />
							<Editing
								allowUpdating={false}
								allowDeleting={false}
								selectTextOnEditStart={true}
								useIcons={true}
							/>
							<HeaderFilter
								visible={false}
							/>
							<FilterPanel
								visible={false}
							/>

							<FilterRow
								visible={false}
							/>
							<Scrolling
								mode="standard"
							/>
							<Column
								width={'35%'}
								dataField="Descripcion"
								cellTemplate="Template1"
							/>
							<Pager
								showPageSizeSelector={true}
								showNavigationButtons={true}
							/>
							<Paging
								enabled={true}
								defaultPageSize={15}
							/>
							<Template name="Template1" render={CellNormal} />
						</TreeList>
						
					</DialogContentText>
				</DialogContent>
				<DialogActions>
					<strong style={{ fontSize: '0.6rem', position: 'absolute', left: '5px', marginLeft: '20px', }}> {monedaSel.Descripcion}</strong>
					<Button1
						variant="primary"
						onClick={seleccionar}
						style={{}}
					>
						<ion-icon name="checkmark-done-outline"></ion-icon>
						Seleccionar
					</Button1>
					<Button1
						variant="secondary"
						onClick={handleClose}
					>
						<ion-icon name="close-outline"></ion-icon>
						Cancelar
					</Button1>
				</DialogActions>
			</Dialog>

		</>
	)
}

export default BuscaMoneda