//import { Modal, Button, ListGroup } from "react-bootstrap";
import { Card, Form, Row, Col } from 'react-bootstrap'
import { useSelector, useDispatch } from 'react-redux';
import Button1 from 'devextreme-react/button';
import TreeList, {
	Pager,
	Paging,
	Editing,
	HeaderFilter,
	FilterPanel,
	FilterRow,
	Scrolling,
	Column
} from 'devextreme-react/tree-list';
import { useEffect, useState } from 'react';
import { Template, TextBox } from 'devextreme-react';
import Pagination from '@material-ui/lab/Pagination';
import { selectUBICACIONES } from '../actions/proyects.actions';
import { CellNormal } from './Cellrend';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Paper from '@mui/material/Paper';
import Draggable from 'react-draggable';


function PaperComponent(props) {
	return (
	  <Draggable
		handle="#draggable-dialog-title"
		cancel={'[class*="MuiDialogContent-root"]'}
	  >
		<Paper {...props} />
	  </Draggable>
	);
  }

const BuscaUbicacion = ({ tipo='',presupuestoN,show, setShow }) => {
	const dispatch = useDispatch();
	const handleClose = () => setShow(false);
	const proyects = useSelector((state) => state.proyects);
	const [ubicacionSel, setUbicacionSel] = useState({
		Codigo: '',
		Descripcion: '',
	});
	const [pagina, setPagina] = useState(1);
	const [tpagina, setTPagina] = useState(1);
	const [textoB, setTextoB] = useState('');

	const seleccionar = () => {
		if (ubicacionSel.Descripcion === '') {
			return;
		}
		presupuestoN.CodLugar = ubicacionSel.Codigo;
		presupuestoN.UbicacionGeografica = ubicacionSel.Descripcion;
		setShow(false);
	}

	useEffect(() => {
		var paginas = localStorage.getItem("paginacion");
		var arrayDeCadenas = [];
		if (paginas)
		arrayDeCadenas = paginas.split('/');

		var totalesp = Math.trunc((arrayDeCadenas[1] / 20));
		if (arrayDeCadenas[1] % 20 !== 0) {
			totalesp = totalesp + 1;
		}
		setTPagina(totalesp);

	}, [proyects.DataUbicaciones, textoB])


	const valueChanged = (data) => {
		setTextoB(data.value);
		dispatch(selectUBICACIONES(data.value, '20', 1, ''));
		setPagina(1);
	}

	const handleChange = (event, value) => {
		setPagina(value);
		//dispatch(limpiaUbicaciones()); 
		dispatch(selectUBICACIONES(textoB, '20', value, ''));
	};
	return (
		<>

			<Dialog
				open={show}
				onClose={handleClose}
				PaperComponent={PaperComponent}
				aria-labelledby="draggable-dialog-title"
				maxWidth={'lg'}
			>
				<DialogTitle style={{ cursor: 'move', background:'#398bf7', color:'white', marginBottom:'10px', height:'50px' }} id="draggable-dialog-title">
				Selecciona una Ubicacin
					<Button1 
						variant="primary"
						onClick={handleClose}
						style={{ position:'absolute', right:'15px', top:'8px',background: '#398bf7', color: 'white'}}
						><i class="fas fa-times"></i>
					</Button1>
        		</DialogTitle>
				<Card style={{ height: '100%', width: '100%', marginTop: '0px'/*, background: '#e8f7fe'*/ }}>
					{/* <Card.Header style={{ height: '38px' }}> <div style={{ marginTop: '-5px' }}>Moneda Principal ({proyects.DatosPresupuesto && proyects.DatosPresupuesto[0] ? proyects.DatosPresupuesto[0].SimboloMoneda : ''}) </div> </Card.Header> */}
					<Card.Body>

						<Form.Group as={Row} className="mb-0" controlId="formHorizontalPassword">
							<Form.Label column sm={6}>
							</Form.Label>
							<Col sm={6}>
								<div className="form mt-0">
									<div className="input-group" data-widget="">
										<TextBox
											defaultValue={textoB}
											value={textoB}
											width="100%"
											showClearButton={true}
											valueChangeEvent="keyup"
											onValueChanged={valueChanged}
											placeholder="Search.."
										>
											<i className="fas fa-search fa-fw" style={{ position: 'absolute', top: '10px', right: '30px', width: '12px', height: '12px' }}></i>

										</TextBox>
									</div>
								</div>
							</Col>
						</Form.Group>
					</Card.Body>
				</Card>				
				<DialogContent >
					
					<DialogContentText>
					<TreeList
						style={{ width: '100%', height: '700px', marginTop: '-15px' }}
						dataSource={proyects.DataUbicaciones}
						keyExpr="CodLugar"
						showBorders={true}
						focusedRowEnabled={true}
						defaultExpandedRowKeys={[1, 2, 3, 5]}
						columnAutoWidth={false}
						rootValue={-1}
						rowAlternationEnabled={true}
						onFocusedRowChanged={(e) => {
							console.log(e)
							setUbicacionSel({
								Codigo: e.row.data.CodLugar,
								Descripcion: e.row.data.Departamento + ' - ' + e.row.data.Descripcion + ' - ' + e.row.data.Provincia,
							});
						}}
						wordWrapEnabled={true}
					>
						<Editing
							allowUpdating={false}
							allowDeleting={false}
							selectTextOnEditStart={true}
							useIcons={true}
						/>
						<HeaderFilter
							visible={false}
						/>
						<FilterPanel
							visible={false}
						/>
						<FilterRow
							visible={false}
						/>
						<Scrolling
							mode="standard"
						/>
						<Column
							width={'33%'}
							dataField="Departamento" 
							cellTemplate="Template1"
							/>
						<Column
							width={'33%'}
							dataField="Descripcion"
							alignment={'left'}
							cellTemplate="Template1"
						/>
						<Column
							width={'33%'}
							dataField="Provincia"
							caption="Provincia"
							alignment={'left'}
							cellTemplate="Template1"
						/>
						<Pager
							showPageSizeSelector={true}
							showNavigationButtons={true}
						/>
						<Paging
							enabled={true}
							defaultPageSize={20}
						/>
						<Template name="Template1" render={CellNormal} />
					</TreeList>
					<div className="" style={{ position: 'relative', width: '100%', height: '30px' }}></div>
					<Pagination count={tpagina} page={pagina} onChange={handleChange} style={{ position: 'absolute', right: '25px', bottom: '55px' }} />
					<strong style={{ fontSize: '0.7rem', position: 'absolute', left: '5px', marginLeft: '20px', bottom: '65px' }}> Pagina</strong>
					<TextBox
						id='TPag'
						style={{ fontSize: '0.7rem', position: 'absolute', left: '25px', marginLeft: '40px', bottom: '55px' }}
						defaultValue={pagina}
						value={pagina}
						width="40px"
						
						valueChangeEvent="keyup"
						onValueChanged={(data) =>{
							if (data.value===''){}
							let pag=Math.trunc(data.value);
							if (data.value!=='' && (pag > 0 && pag <= tpagina)){
								setPagina(pag);
								dispatch(selectUBICACIONES(textoB, '20', data.value, ''));
							}else{}
						}}
						placeholder={pagina}
					>
					</TextBox>
					</DialogContentText>
				</DialogContent>
				<DialogActions>
				<strong style={{ fontSize: '0.6rem', position: 'absolute', left: '5px', marginLeft: '20px', }}> {ubicacionSel.Descripcion}</strong>
					<Button1
						variant="primary"
						onClick={seleccionar}
						style={{ }}
					>
						<ion-icon name="checkmark-done-outline"></ion-icon>
						Seleccionar
					</Button1>
					<Button1
						variant="secondary"
						onClick={handleClose}
					>
						<ion-icon name="close-outline"></ion-icon>
						Cancelar
			</Button1>
				</DialogActions>
			</Dialog>

		</>
	)
}

export default BuscaUbicacion
