
import {
	APU_DETALLE_CATALOGO1,
	APU_DETALLE_CATALOGO2,
	LISTAR_PARTIDAS2,LISTAR_PARTIDAS21,LISTAR_PARTIDAS22,LISTAR_PARTIDASC, LISTAR_PARTIDASC1, LISTAR_PARTIDASC2,
} from '../constants';

const initialState = {
	DataPartidas: [],
	DataPartidasAgre: [],
	DataPartidasAgre1: [],
	DataApuCatalogo: [],
	DataApuCatalogo1: [],
	SumasApuCatalogo: '',		
	SumasApuCatalogo1: '',		
};

// eslint-disable-next-line
export default (state = initialState, { type, payload }) => {
	//alert('se dispara ' + payload);
	switch (type) {

		case LISTAR_PARTIDASC:
			return {
				...state,
				DataPartidas: [...payload]
			};


		case LISTAR_PARTIDAS2:
			return {
				...state,
				DataPartidas: [...state.DataPartidas, ...payload]
			};


		case LISTAR_PARTIDASC1:
			return {
				...state,
				DataPartidasAgre: [...payload]
			};


		case LISTAR_PARTIDAS21:
			return {
				...state,
				DataPartidasAgre: [...state.DataPartidasAgre, ...payload]
			};

		case LISTAR_PARTIDASC2:
			return {
				...state,
				DataPartidasAgre1: [...payload]
			};


		case LISTAR_PARTIDAS22:
			return {
				...state,
				DataPartidasAgre1: [...state.DataPartidasAgre1, ...payload]
			};
		

		case APU_DETALLE_CATALOGO1:
			return {
				...state,
				SumasApuCatalogo: payload.sumas,
				DataApuCatalogo: payload.data
			};

			case APU_DETALLE_CATALOGO2:
				return {
					...state,
					SumasApuCatalogo1: payload.sumas,
					DataApuCatalogo1: payload.data
				};
	
	
		default:
			return state
	};
}
